import * as React from "react";
import {ChangeEvent} from "react";
import styled from "styled-components";

interface TextInputProps {

}

interface TextInputState {
    value: string
}

export default class TextInput extends React.Component<TextInputProps, TextInputState> {
    constructor(p: TextInputProps) {
        super(p);

        this.state = {
            value: ""
        }
    }

    handleChange(e: ChangeEvent<HTMLInputElement>) {
        this.setState({
            value: e.target.value
        });
    }

    render() {
        return <BaseInput type="text" onChange={(e) => this.handleChange(e)} value={this.state.value}/>
    }
}

const BaseInput = styled.input`
    width: 200px;
    height: 30px;
    background-color: ${props => props.theme.login.input.bg};
    border: none;
    padding: 5px;
    padding-right: 10px;
    padding-left: 10px;
    border-radius: 5px;
    font-size: 15px;
    color: ${props => props.theme.login.input.color};
`
